import uniLoadMore from "@/components/uni-load-more.vue"
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			lists:'',
			authKey:'',
			loadingType: 0,
			next : -1,
			start: 0,
			size: 15,
			record: false,
		};
	},
	onLoad(e) {
		this.authKey = uni.getStorageSync('authKey');
		this.join();
	},
	methods:{
		// 合伙人收益明细
		join(isLoading = true){
			var that = this;
			let querys = "?start=" + this.start + "&size=" + this.size 
			uni.request({
				url: this.http+'copemate/incomes' + querys,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					let data = res.data.data
					if (res.data.code == 200) {
						let order = data.list
						console.log(order)
						if (order.length == 0) {
							that.loadingType = 2
							order = []
						}
						else if (order.length < that.size)
							that.loadingType = 2
						else 
							that.loadingType = 0
						
						if (that.start == 0) 
							that.lists = []

						that.lists = that.lists.concat(order)
						if (that.lists.length == 0) 
							that.record = true
						else 
							that.record = false
						
					}
					that.start = data.next	
				},
			});
		},
		lower(){
			if (this.loadingType == 2) {
				return
			}
			this.requestLearnList(false)
		}
	}
}
